.section {
  display: flex;
  padding: 5rem;
  height: 90rem;
  transition: all 2s;
  opacity: 1;

  @include respond(smallest) {
    padding: 2rem;
  }
}
.section--hidden {
  opacity: 0;
  transform: translateY(3rem);
}
.document--container {
  margin: 0 auto;
  max-width: 210rem;
  overflow: hidden;
  background-color: var(--tertiary-color);
  border: 2px solid var(--secondary-color--dark);
  @media screen and (max-width: 22.5rem) {
    padding-top: 0;
  }
}
