/* ------------------------  Gallerie -------------------- */
.gallery__section {
  height: 120rem !important;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.gallery {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  height: 90rem;
}

.gallery__container {
  padding-bottom: 3rem;
  display: flex;
  position: relative;
  width: 80%;
  height: 100%;
  background: #fff;
  border: 3px solid #f9f9f8;
  border-radius: 5px;
  overflow: hidden;
}

.main-image-section {
  width: 80%;
  height: 80%;
}

.main-view {
  display: grid;
  width: 100%;
  height: 90rem;
  object-fit: cover;
  opacity: 0;
  cursor: pointer;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  transition: all 1s ease;
}

.caption-container {
  position: absolute;
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100px;
  bottom: -100px;
  opacity: 0;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  transition: all 1s ease;
}

.main-image-section:hover .caption-container {
  bottom: 0;
  opacity: 1;
}

#caption {
  font-size: 42px;
  color: #fff;
  padding: 10px;
}

#info {
  padding: 0 10px;
  color: #ccc;
}

.thumbnails {
  height: 100%;
  width: 20%;
  padding: 5px;
  overflow-y: auto;
}

.thumbnails img {
  width: 100%;
  opacity: 0.7;
  cursor: pointer;

  -webkit-transition: opacity 0.3s ease 0s;
  -moz-transition: opacity 0.3s ease 0s;
  transition: opacity 0.3s ease 0s;
}

.thumbnails img:hover {
  opacity: 1;
}

.thumbnails img:focus {
  opacity: 1;
}
