.slider::before {
  background-image: linear-gradient(60deg, #f5eb71, #fffaa8, #abf25f);
  content: "";
  position: absolute;
  width: 100rem;
  left: 20%;
  height: 20rem;
  top: 10rem;
  display: block;
  background-size: 400% 400%;
  filter: blur(15rem);
  background-position: 0 50%;
  background-repeat: no-repeat;
}

.slider {
  margin-top: 5rem;
  height: 60%;
  width: 100%;
  position: relative;

  &-btn {
    position: absolute;
    display: flex;
    top: 35%;
    z-index: 50;
    padding: 2rem;
    cursor: pointer;

    @include respond(IpadPro12) {
      top: 25%;
    }
    @include respond(IPadAir) {
      top: 45%;
    }
    @include respond(IPhone12) {
      top: 50%;
      padding: 1rem;
    }

    @include respond(IPhone5) {
      top: 130%;
      padding: 0.5rem;
    }

    &--left {
      left: 3%;
      @include respond(IpadPro12) {
        left: -2%;
      }
      @include respond(IPadAir) {
        left: -5%;
      }
      @include respond(MicrosoftSurface) {
        left: -15%;
      }
      @include respond(IPhone5) {
        left: 30%;
      }
    }

    &--right {
      right: 3%;
      @include respond(IpadPro12) {
        right: -2%;
      }
      @include respond(IPadAir) {
        right: -5%;
      }
      @include respond(MicrosoftSurface) {
        right: -15%;
      }
      @include respond(IPhone5) {
        right: 30%;
      }
    }
  }
  &-btn--left img {
    transform: translateX(0.5rem);
  }
  &-dots {
    margin: 0 auto;
    position: absolute;
    bottom: -10%;
    left: 45%;
    @include respond(IpadPro12) {
      bottom: 10%;
    }
    @include respond(IPadAir) {
      bottom: -50%;
      left: 40%;
    }
    @include respond(IPhone12) {
      bottom: -20%;
      left: 30%;
    }
    @include respond(IPhone5) {
      top: 115%;
      left: 25%;
    }
  }
  &-dot {
    height: 1rem;
    width: 1rem;
    margin-right: 1.75rem;
    cursor: pointer;
    transition: all 0.5s;
    border: 2px solid var(--secondary-color--dark);

    &__active {
      background-color: var(--secondary-color--dark);
    }
  }
}
.component {
  display: flex;
  position: relative;
  height: 90%;

  width: 80%;
  overflow: hidden;
  margin: 0 auto;

  @include respond(IpadPro12) {
    height: 75%;
    border-radius: 10px;
  }
  @include respond(IPadAir) {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr;
    content: "";
    height: 50rem;
  }
  @include respond(IPhone12) {
    width: 100%;
  }
}
.components {
  border: 2px solid var(--secondary-color--dark);
  display: grid;
  background-color: var(--white);
  overflow: hidden;
  width: 100%;
  height: 37rem;
  margin: 0 auto;
  grid-template-columns: 30% 70%;
  position: absolute;
  margin: 0 auto;
  transition: all 1s;

  @include respond(IPadAir) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    width: 100%;
  }
  @include respond(MicrosoftSurface) {
    grid-template-rows: 45% 55%;
  }
  @include respond(IPhone8) {
    grid-template-rows: 35% 65%;
  }
  @include respond(IPhone5) {
    grid-template-rows: 30% 70%;
    height: 45rem;
  }
}
.component-left {
  display: flex;
  justify-items: flex-start;

  @include respond(IPadAir) {
    justify-content: center;
  }

  &--photo {
    width: 35rem;
    object-fit: cover;
    height: 40rem;

    @include respond(IpadPro12) {
      width: 30rem;
    }
    @include respond(IPadAir) {
      border-radius: 10px;
      margin: 0 auto;
      max-height: 25rem;
      width: 25rem;
    }
    @include respond(MicrosoftSurface) {
      max-height: 20rem;
      width: 20rem;
      max-height: 20rem;
    }
    @include respond(IPhone12) {
      width: 15rem;
      max-height: 15rem;
    }
    @include respond(IPhone5) {
      width: 10rem;
      max-height: 10rem;
    }
  }
}

.component-right {
  padding: 2rem;
  display: flex;
  margin-right: 5rem;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-content: center;
  @include respond(pc-small) {
    margin-right: 0;
    margin-left: 4rem;
    padding: 2.5rem;
  }
  @include respond(MacBookAir) {
    padding: 2rem 5rem;
    margin-left: 5rem;
    transform: translateY(-2rem);
  }
  @include respond(tab-land-small) {
    padding-right: 1rem;
  }
  @include respond(IPadAir) {
    margin-left: 0;
    padding: 2.5rem 2rem 2rem 2rem;
    justify-content: flex-start;
  }
  @include respond(MicrosoftSurface) {
    padding: 1rem 2rem 2rem 2rem;
  }
  @include respond(IPhone5) {
    padding: 0;
  }

  &--text {
    margin: 0;

    @include respond(tab-land-small) {
      font-size: 1.4rem;
      padding-left: 3rem;
    }
    @include respond(IpadPro12) {
      font-size: 1.5rem;
      padding-left: 1rem;
    }
    @include respond(IPadAir) {
      font-size: 1.3rem;
    }
    @include respond(IPhone12) {
      font-size: 1.2rem;
      margin-top: 1rem;
    }
    @include respond(IPhone5) {
      margin-top: 2rem;
      font-size: 1.1rem;
    }
  }
  &--name {
    margin-top: 2rem;
    font-weight: bold;
    margin-bottom: 0;
    @include respond(tab-land-small) {
      font-size: 1.6rem;
    }
    @include respond(MicrosoftSurface) {
      font-size: 1.4rem;
      margin-top: 0.5rem;
    }
  }

  &--position {
    margin: 0;
    color: rgb(66, 66, 66);
    @include respond(tab-land-small) {
      font-size: 1.6rem;
    }
    @include respond(MicrosoftSurface) {
      font-size: 1.4rem;
      margin-bottom: 1rem;
    }
    @include respond(IPhone5) {
      padding-bottom: 10rem;
    }
  }
}
