/* ----------------------- Programms ------------------------- */

.programms {
  background-color: var(--white);
  height: 90rem;
  margin-bottom: 20rem;
  display: flex;
  align-items: center;
  justify-items: center;
  &__container {
    display: grid;
    padding: 15rem 10rem 15rem 10rem;
    grid-gap: 5rem;
    margin: 0 auto;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    width: 100%;

    @include respond(tab-land-small) {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
    }
    @include respond(IpadPro12) {
      grid-gap: 2rem;
      padding: 3rem;
      padding-bottom: 20rem;
    }
    @include respond(SamsungGalaxyTab3) {
      padding-bottom: 10rem;
    }
  }

  &--text-area {
    padding: 5rem;
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-items: center;
    grid-auto-rows: 1/-1;
    grid-column: 1/2;

    @include respond(SamsungGalaxyTab3) {
      padding-right: 2rem;
      padding-left: 2rem;
    }
    @include respond(IPhone5) {
      padding: 1rem;
    }
  }

  &--imgs {
    padding: 5rem;
    grid-row: 1/-1;
    grid-column: 2/3;
    display: flex;
    flex-direction: column;
    position: relative;
    left: 15rem;
    @include respond(tab-land-small) {
      grid-row: 2/3;
      grid-column: 1/-1;
    }

    @include respond(SamsungGalaxyTab3) {
      padding: 2rem;
    }
    @include respond(IPhone5) {
      padding: 1rem;
    }
  }
}

