//button
.btn-text:link,
.btn-text:visited {
  font-family: kimperly-kg;
  font-size: 2.2rem;
  display: inline-block;
  text-decoration: none;
  padding: 1.5rem;
  transition: all 1s;
  color: var(--secondary-color--dark);

  @include respond(IPhone5) {
    font-size: 1rem;
  }
}
.btn-text:hover {
  transform: translateY(-2px);
  background-image: linear-gradient(
    60deg,
    hsl(224, 85%, 66%),
    hsl(269, 85%, 66%),
    hsl(314, 85%, 66%),
    hsl(359, 85%, 66%),
    hsl(44, 85%, 66%),
    hsl(89, 85%, 66%),
    hsl(134, 85%, 66%),
    hsl(179, 85%, 66%)
  );
  -webkit-background-clip: text;
  color: transparent;
}
.btn-text:active {
  transform: traslateY(-2px);
}
